import React, {useEffect, useState} from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';
import axios from "axios";
import Swal from "sweetalert2";

Chart.register(...registerables);

const Grafica = () => {

    const [data, setData] = useState({
        labels: [],
        datasets: [
            {
                label: 'PIB Anual Promedio',
                data: [],
                backgroundColor: 'rgba(255, 99, 132, 0.5)',
            }
        ]
    })

    useEffect( () => {
        let config = {
            headers : {
                origin : window.location.host
            }
        }
        const fetchData = async() => {
            await axios.get('https://backendg18redes.tk/getEstadisticas', config)
                .then(response => {
                    let pib_anual_promedio = response.data['estadisticas']['pib_anual_promedio']
                    console.log(pib_anual_promedio)
                    setData({
                        labels: pib_anual_promedio.map( (e) => e['anio']),
                        datasets: [
                            {
                                label: 'PIB Anual Promedio',
                                data: pib_anual_promedio.map( (e) => e['avg(variacion_interanual)']),
                                backgroundColor: 'rgba(' + process.env.REACT_APP_R + ', '
                                    + process.env.REACT_APP_G + ', ' + process.env.REACT_APP_B + ', 0.5)',
                            }
                        ]
                    })
                })
                .catch(() => {
                    Swal.fire("Error", "Error while fetching data", "error").then()
                })
        }

        fetchData()
    }, [])

    return (
        <Bar
            data={data}
            width={600}
            height={400}
            options={
                {
                    maintainAspectRatio: false
                }
            }
        />
    )
}

export default Grafica;