import Navigation from "../components/Navigation";
import {Carousel, Container} from "react-bootstrap";
import {useEffect, useState} from "react";
import Swal from "sweetalert2";
import axios from "axios";


const Landing = () => {

    const [images, setImages] = useState([
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        },
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        },
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        },
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        },
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        }
    ])

    useEffect( () => {
        let config = {
            headers : {
                origin : window.location.host
            }
        }

        const instancia = axios.create({
            baseURL : window.location.host,
            headers : {'User-Agent' : window.location.host, 'Host' : window.location.host, 'Origin' : window.location.host},
            timeout: 10000
        })

        instancia.defaults.baseURL = window.location.host;
        console.log(config)
         const fetchData = async() => {
             await instancia.get('https://backendg18redes.tk/getImagenes')
                 .then(response => {
                     console.log(response.data)
                     setImages(response.data)
                 })
                 .catch(() => {
                     Swal.fire("Error", "Error while fetching data", "error").then()
                 })
         }

         fetchData()
    }, [])

    return (
        <>
            <Navigation></Navigation>
            <Container fluid={"md"}>
                <Carousel>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src={images[0].url_imagen}
                            alt={images[0].nombre_funcion}
                            width={500} height={500}
                        />
                        <Carousel.Caption>
                            <h3>{images[0].descripcion}</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src={images[1].url_imagen}
                            alt={images[1].nombre_funcion}
                            width={500} height={500}
                        />
                        <Carousel.Caption>
                            <h3>{images[1].descripcion}</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src={images[2].url_imagen}
                            alt={images[2].nombre_funcion}
                            width={500} height={500}
                        />
                        <Carousel.Caption>
                            <h3>{images[2].descripcion}</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src={images[3].url_imagen}
                            alt={images[3].nombre_funcion}
                            width={500} height={500}
                        />
                        <Carousel.Caption>
                            <h3>{images[3].descripcion}</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src={images[4].url_imagen}
                            alt={images[4].nombre_funcion}
                            width={500} height={500}
                        />
                        <Carousel.Caption>
                            <h3>{images[4].descripcion}</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
            </Container>
        </>
    )
}

export default Landing;