import Navigation from "../components/Navigation";
import {Card, Container, Row, Col} from "react-bootstrap";
import {useEffect, useState} from "react";
import Swal from "sweetalert2";
import axios from "axios";
import Grafica from "../components/Grafica";


const DesarrolloEconomico = () => {

    const [images, setImages] = useState([
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        },
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        },
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        },
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        },
        {
            url_imagen: '',
            nombre_funcion: '',
            descripcion: '',
        }
    ])

    const [noticias, setNoticias] = useState([
        {
            nombre: '',
            descripcion: ''
        },
        {
            nombre: '',
            descripcion: ''
        }
    ])

    useEffect( () => {
        let config = {
            headers : {
                origin : window.location.host
            }
        }
        const fetchData = async() => {
            await axios.get('https://backendg18redes.tk/getEstadisticas', config)
                .then(response => {
                    console.log(response.data)
                    setImages(response.data)
                })
                .catch(() => {
                    Swal.fire("Error", "Error while fetching data", "error").then()
                })
        }

        fetchData()
    }, [])

    return (
        <>
            <Navigation></Navigation>
            <Container className={"mt-5 mb-5"}>
                <Grafica></Grafica>
            </Container>
        </>
    )
}

export default DesarrolloEconomico;