import React from 'react';
import{ BrowserRouter as Router,  Switch, Route } from "react-router-dom"

import Landing from '../pages/Landing'
import Administradores from '../pages/Administradores'
import Desarrolladores from '../pages/Desarrolladores';
import DesarrolloEconomico from "../pages/DesarrolloEconomico"

function Routes() {
    return (
        <Router>
            <Switch>
                <Route exact path="/" component={Landing}/>
                <Route exact path="/admin"  component={Administradores}/>
                <Route exact path="/developers"  component={Desarrolladores}/>
                <Route exact path="/desarrollo_economico"  component={DesarrolloEconomico}/>
            </Switch>
        </Router>
    );
}

export default Routes;