import Navigation from "../components/Navigation";
import {Container, Row, Col, Card} from "react-bootstrap";
import {useEffect, useState} from "react";
import axios from "axios";
import Swal from "sweetalert2";


const Desarrolladores = () => {

    const [developers, setDevelopers] = useState([
        {
            nombre: '',
            carnet: ''
        },
        {
            nombre: '',
            carnet: ''
        },
        {
            nombre: '',
            carnet: ''
        },
        {
            nombre: '',
            carnet: ''
        }
    ])

    useEffect( () => {
        let config = {
            headers : {
                origin : window.location.host
            }
        }
        const fetchData = async() => {
            await axios.get('https://backendg18redes.tk/getIntegrantes', config)
                .then(response => {
                    console.log(response.data)
                    setDevelopers(response.data['result'])
                })
                .catch(() => {
                    Swal.fire("Error", "Error while fetching data", "error").then()
                })
        }

        fetchData()
    }, [])

    return (
        <>
            <Navigation></Navigation>
            <Container className="mt-5">
                <Row>
                    <Col>
                        <Card style={{ width: '16rem' }}>
                            <Card.Body>
                                <Card.Title>{developers[0].nombre}</Card.Title>
                                <Card.Text>
                                    {developers[0].carnet}
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card style={{ width: '16rem' }}>
                            <Card.Body>
                                <Card.Title>{developers[1].nombre}</Card.Title>
                                <Card.Text>
                                    {developers[1].carnet}
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card style={{ width: '16rem' }}>
                            <Card.Body>
                                <Card.Title>{developers[2].nombre}</Card.Title>
                                <Card.Text>
                                    {developers[2].carnet}
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card style={{ width: '16rem' }}>
                            <Card.Body>
                                <Card.Title>{developers[3].nombre}</Card.Title>
                                <Card.Text>
                                    {developers[3].carnet}
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default Desarrolladores;