import Navigation from "../components/Navigation";
import {Container, Row, Col, Card} from "react-bootstrap";
import {useEffect, useState} from "react";
import axios from "axios";
import Swal from "sweetalert2";


const Administradores = () => {

    const [admins, setAdmins] = useState([
        {
            nombre: '',
            puesto: '',
            descripcion: '',
        },
        {
            nombre: '',
            puesto: '',
            descripcion: '',
        },
        {
            nombre: '',
            puesto: '',
            descripcion: '',
        },
        {
            nombre: '',
            puesto: '',
            descripcion: '',
        }
    ])

    useEffect( () => {
        let config = {
            headers : {
                origin : window.location.host
            }
        }

        axios.defaults.headers.common['Host'] = window.location.host;
        const fetchData = async() => {
            await axios.get('https://backendg18redes.tk/getAdmins')
                .then(response => {
                    console.log(response.data)
                    setAdmins(response.data)
                })
                .catch(() => {
                    Swal.fire("Error", "Error while fetching data", "error").then()
                })
        }

        fetchData()
    }, [])

    return (
        <>
            <Navigation></Navigation>
            <Container className="mt-5">
                <Row>
                    <Col>
                        <Card style={{ width: '16rem' }}>
                            <Card.Body>
                                <Card.Title>{admins[0].nombre}</Card.Title>
                                <Card.Text>
                                    {admins[0].puesto}
                                    <p>
                                        {admins[0].descripcion}
                                    </p>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card style={{ width: '16rem' }}>
                            <Card.Body>
                                <Card.Title>{admins[1].nombre}</Card.Title>
                                <Card.Text>
                                    {admins[1].puesto}
                                    <p>
                                        {admins[1].descripcion}
                                    </p>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card style={{ width: '16rem' }}>
                            <Card.Body>
                                <Card.Title>{admins[2].nombre}</Card.Title>
                                <Card.Text>
                                    {admins[2].puesto}
                                    <p>
                                        {admins[2].descripcion}
                                    </p>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card style={{ width: '16rem' }}>
                            <Card.Body>
                                <Card.Title>{admins[3].nombre}</Card.Title>
                                <Card.Text>
                                    {admins[3].puesto}
                                    <p>
                                        {admins[3].descripcion}
                                    </p>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default Administradores;